"use client";

import React, { useState, useEffect } from 'react';
import './Offres.css';
import LoadingSpinner from '../components/LoadingSpinner';
import ErrorMessage from '../components/ErrorMessage';

const Offres = () => {
    const [offres, setOffres] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        fetchOffres();
    }, []);

    const fetchOffres = async () => {
        try {
            const response = await fetch('/api/offres');
            if (!response.ok) {
                throw new Error('Erreur lors du chargement des offres');
            }
            const data = await response.json();
            setOffres(data);
            setLoading(false);
        } catch (err) {
            setError(err.message);
            setLoading(false);
        }
    };

    const handlePostuler = async (offreId) => {
        try {
            console.log(`Postulation pour l'offre ${offreId}`);
        } catch (err) {
            console.error('Erreur lors de la postulation:', err);
        }
    };

    if (loading) return <LoadingSpinner />;
    if (error) return <ErrorMessage message={error} />;

    return (
        <div className="offres-container">
            <h1 className="page-title">Nos offres d'emploi</h1>
            
            <div className="offres-grid">
                {offres.map((offre) => (
                    <article key={offre.id} className="offre">
                        <h2>{offre.titre}</h2>
                        <div className="offre-details">
                            <p><strong>Entreprise:</strong> {offre.entreprise}</p>
                            <p><strong>Localisation:</strong> {offre.localisation}</p>
                            <p><strong>Type de contrat:</strong> {offre.typeContrat}</p>
                            <p><strong>Salaire:</strong> {offre.salaire}</p>
                            <p>{offre.description}</p>
                        </div>
                        <button 
                            className="postuler-button"
                            onClick={() => handlePostuler(offre.id)}
                        >
                            Postuler
                        </button>
                    </article>
                ))}
            </div>
        </div>
    );
};

export default Offres;
